$firstColor: #5465ff;

.pevItemAction {
	display: flex;

	>button {
		font-weight: 600;
		color: #fff;
		display: flex;
		align-items: center;
		gap: 5px;
		transition: all .15s linear;

		&:hover,
		&:focus,
		&:active {
			color: #fff !important;
			outline: 0 !important;
			transform: translateY(-2px);
			box-shadow: rgba(27, 31, 35, 0.04) 0px 1px 0px, rgba(255, 255, 255, 0.25) 0px 1px 0px inset;
		}
	}

	.duplicateItem {
		background-color: $firstColor;
	}

	.removeItem {
		background-color: red;
	}
}

.pevAddItem {
	text-align: center;

	button {
		font-weight: 600;
		color: #fff;
		background-color: $firstColor;
		display: inline-flex;
		align-items: center;
		gap: 5px;
		transition: all .15s linear;

		&:hover,
		&:focus,
		&:active {
			color: #fff !important;
			outline: 0 !important;
			transform: translateY(-2px);
			box-shadow: rgba(27, 31, 35, 0.04) 0px 1px 0px, rgba(255, 255, 255, 0.25) 0px 1px 0px inset;
		}

		.dashicon {
			height: 16px !important;
		}
	}
}