$firstColor: #5465ff;
$secondColor: #788bff;
$thirdColor: #9bb1ff;
$fourthColor: #bfd7ff;
$fifthColor: #e2fdff;

$numbers: 0, 3, 5, 10, 15, 20, 25, 30, 35;

@each $num in $numbers {
	.gap#{$num} {
		gap: $num * 1px !important;
	}

	.mt#{$num} {
		margin-top: $num * 1px !important;
	}

	.mr#{$num} {
		margin-right: $num * 1px !important;
	}

	.mb#{$num} {
		margin-bottom: $num * 1px !important;
	}

	.ml#{$num} {
		margin-left: $num * 1px !important;
	}
}

// Inspector Info
.pevInspectorInfo {
	padding: 6px;

	a {
		text-decoration: none;
		font-weight: bold;
	}
}

// Tab Panel
.pevTabPanel {
	.components-tab-panel__tabs {
		display: flex;
		justify-content: space-between;

		button {
			// font-size: 16px;
			color: $firstColor;
			width: 50%;
			justify-content: center;

			&:focus {
				color: $firstColor;
				box-shadow: none;
			}
		}

		.activeTab {
			border-bottom: 3px solid $firstColor;
			font-weight: 700;
			background-color: rgba($firstColor, .15);
		}
	}
}

.pevPanelBody,
.pevDropdownPopover {
	.components-base-control {
		margin-bottom: 0 !important;

		.components-base-control__field {
			padding: 0;
			margin: 0 !important;
		}
	}

	// .pevBoxControl {
	// 	.components-base-control.components-range-control {
	// 		display: none;
	// 	}
	// }

	.pevResetContainer {
		display: flex;
		align-items: center;
		gap: 10px;

		.pevResetBtn {
			width: 24px;
			min-width: auto;
			height: 24px;
			padding: 0;
		}
	}
}

.pevEmptyBlockAppender {
	display: flex;
	align-items: center;
	justify-content: center;
	border: 1px dashed $secondColor;

	button {
		border: none;
		outline: 0;
		box-shadow: none;

		svg {
			width: 36px;
			height: 36px;
			fill: $secondColor;
		}

		&:hover,
		&:focus {
			border: none;
			outline: 0 !important;
			box-shadow: none !important;

			svg {
				fill: $firstColor;
			}
		}
	}
}