$firstColor: #5465ff;

.pevDeviceControl {
	display: inline-flex;
	align-items: center;
	gap: 2px;

	button {
		width: 22px;
		height: 22px;
		color: #b8b8b8;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		padding: 5px;

		&:hover {
			color: #333;
		}

		&.active {
			color: $firstColor;
		}
	}
}