$firstColor: #5465ff;
$secondColor: #788bff;

.pevBtnGroup {
	button {
		color: #333;
		color: $firstColor !important;
		background: #fff;
		font-size: 13px;
		line-height: 13px;
		font-weight: bold;
		border: 0;
		box-shadow: inset 0 0 0 1px $firstColor !important;
		outline: 0;
		transition: all .2s linear;

		&:hover,
		&:focus {
			color: #fff !important;
			background: $secondColor;
			border: 0 !important;
			outline: 0 !important;
		}

		&.is-primary {
			color: #fff !important;
			background: $firstColor !important;
			box-shadow: inset 0 0 0 1px $secondColor !important;
		}
	}
}