$firstColor: #5465ff;
$secondColor: #788bff;

.pevInlineMediaControl {
	.components-panel__row {
		>*:first-child {
			flex: 1;
		}

		button {
			width: 31px;
			height: 31px;
			display: inline-block;
			color: #fff;
			background: $firstColor;
			padding: 0;
			border: none;
			outline: 0;

			&:hover,
			&:active {
				color: #fff;
				background: $secondColor;
			}
		}
	}
}